import {
  requestDELETE,
  requestGET,
  requestPOST,
  requestPUT,
} from "@utils/axios";
import { Api } from "@constants";
import { IProductsParams } from "@interfaces";

const getProductService = (params: IProductsParams) =>
  requestGET(Api.product + "/filtration", { ...params, pageSize: 7 });

const getProductByIdService = (id: string) =>
  requestGET(Api.product + "/" + id);

const createBannerService = (body: FormData) => requestPOST(Api.product, body);

const deleteBannerService = (id: string) => requestDELETE(Api.product, id);

const editBannerService = (body: FormData, id: string) =>
  requestPUT(Api.product, body, id);

export const productServices = Object.freeze(
  Object.seal({
    get: getProductService,
    getById: getProductByIdService,
    create: createBannerService,
    delete: deleteBannerService,
    edit: editBannerService,
  })
);
